export default{
	data(){
		return{
			showPop:false
		}
	},
	watch: {
		show(newValue) {
			if (newValue) {
				this.showPop = true;
			} else {
				this.showPop = false;
			}
		}
	},
	props:{
		show:{
			type: Boolean,
			default: false
		},
		title:{  //标题
			type:String,
			default:'操作提示'
		}
	},
	methods:{
		changed(e) {
			if (!e.show) {
				this.showPop = false;
			}
		},
		closePop(){
			this.showPop = false;
			this.$emit('change', {
				show: false
			})
		},
		confirmCb(){  //确认回调函数
			this.closePop();
			this.$emit('confirm')
		}
	}
}
